import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/TheLogin'
import Container from '@/container/Container'
import Dashboard from '@/views/dashboard'
import Article from '@/views/article'
import Jira from '@/views/jira'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
	{
      path: '/',
	  redirect: '/dashboard',
      name: 'Container',
      component: Container,
	  children: [
		{path: 'dashboard', name: 'router首页', component: Dashboard, },
		{path: 'article', name: 'router文章', component: Article, },
		{path: 'jira', name: '速记BUG', component: Jira, },
      ]
    }
  ]
})


